<template>
  <div class="container-fluid d-flex align-items-center justify-content-center">
    <div class="cardSignUp container">
      <div class="lenta">
        <span class="span1"></span><span class="span2"></span><span class="span3"></span>
        <h1 class="mt-4">Elektrom pochtani ro'yhatdan o'tkazish</h1>
      </div>
      <div class="alert alert-info mx-4 my-2" role="alert">
        Ro'yhatdan o'tish uchun elektron pochtangizni kiriting, biz pochta manzilingizga
        tasdiqlash kodini jo'natamiz
      </div>
      <form class="p-4">
        <div class="input-group mb-3">
          <input
            @keyup.enter="sendTo"
            v-model="regEmail"
            :class="{
              'is-valid': sendKey,
              'is-invalid': emailError || doubleEmail,
            }"
            :disabled="sendKey && !emailError && !doubleEmail"
            type="email"
            class="form-control"
            placeholder="Elektron pochta"
            aria-label="Elektron pochta"
            aria-describedby="button-addon2"
          />
          <button
            @click="sendTo"
            :class="{
              'border-success': sendKey,
              'border-danger': emailError || doubleEmail,
            }"
            :disabled="sendKey"
            class="btn btn-outline-secondary alert-success"
            type="button"
            id="button-addon2"
          >
            Yuborish
          </button>
        </div>
        <div v-if="sendKey" class="alert alert-success border-success my-2" role="alert">
          Elektron pochtaga oraqali tasdiqlash kodi jo'natildi. Iltimos tasdiqlash kodini
          kiriting
        </div>
        <div v-if="emailError" class="alert alert-danger" role="alert">
          Elektron pochtani kiritishda xatolik!
        </div>
        <div v-if="doubleEmail" class="alert alert-danger" role="alert">
          Bunday elektron pochta allaqachon ro'yhatdan o'tgan
        </div>
        <div class="input-group mb-3">
          <input
            @keyup.enter="tekshirish"
            :disabled="!sendKey || trueKey"
            v-model="getKey"
            type="text"
            :class="{ 'is-valid': trueKey, 'is-invalid': falseKey }"
            class="form-control"
            placeholder="Tasdiqlash kodi"
            aria-label="Tasdiqlash kodi"
            aria-describedby="button-addon2"
          />
          <button
            :disabled="!sendKey || trueKey"
            :class="{ 'border-danger': falseKey, 'border-success': trueKey }"
            @click="tekshirish"
            class="btn btn-outline-secondary alert-success"
            type="button"
            id="button-addon2"
          >
            Tasdiqlash
          </button>
        </div>
        <div v-if="keyError" class="alert alert-danger" role="alert">
          Tasdiqlash kodi xato, qayta urunib ko'ring
        </div>
        <router-link to="/sign_up" class="btn btn-success"> Davom etish</router-link>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Test",
  created() {},
  data() {
    return {
      regEmail: "",
      getKey: "",
      doubleTest: false,
      passwordSee: true,
      tasdiqlashKodi: "1111",
      emailError: false,
      doubleEmail: false,
      keyError: false,
      sendKey: false,
      trueKey: false,
      falseKey: false,
      regList: [
        {
          email: "hrmxan@gmail.com",
        },
        {
          email: "hrmxanuzm@gmail.com",
        },
      ],
    };
  },
  props: {},
  methods: {
    see1() {
      this.passwordSee = !this.passwordSee;
      if (this.passwordSee) this.$refs.inputP.type = "password";
      else this.$refs.inputP.type = "text";
    },
    sendTo() {
      if (this.regEmail.indexOf("@") != -1 && this.regEmail.indexOf(".") != -1) {
        if (this.regEmail.indexOf(".") - this.regEmail.indexOf("@") > 0) {
          this.regList.filter((person) => {
            if (person.email == this.regEmail) {
              this.doubleTest = true;
            }
          });
          if (this.doubleTest) {
            this.doubleEmail = true;
            this.emailError = false;
          } else {
            this.sendKey = true;
            this.emailError = false;
            this.doubleEmail = false;
          }
        } else return;
      } else {
        this.emailError = true;
      }
    },
    tekshirish() {
      if (this.getKey == this.tasdiqlashKodi) {
        this.trueKey = true;
        localStorage.setItem("onKey", true);
        this.falseKey = false;
      } else {
        this.trueKey = false;
        this.falseKey = true;
      }
    },
  },
  watch: {
    regEmail() {
      this.doubleTest = false;
    },
    getKey() {
      this.trueKey = false;
      this.falseKey = false;
    },
  },
};
</script>

